<template>
<div class="login" style="padding:10px">
  ログイン処理中です
</div>
</template>

<script>
import { getAuth, setPersistence, TwitterAuthProvider, signInWithRedirect, browserLocalPersistence } from 'firebase/auth'
import axios from 'axios'

export default {
  name: 'Login',
  components: {
  },
  data() {
    return {
    };
  },
  computed : {
		
  },
  mounted () {
    getAuth().onAuthStateChanged((user) => {
      if (user) {
        console.log('login!!');
        console.log(user);

        let params = new URLSearchParams();
        // params.append('twitterUID', 'IbLuEgfCgTfhSoOHMFi8h6hLrry1');
        let uid = user.providerData[0].uid;
        let iconUrl = user.providerData[0].photoURL;
        let displayName = user.providerData[0].displayName;
        let displayId = user.reloadUserInfo.providerUserInfo[0].screenName;

        params.append('twitterUID', uid);
        params.append('twitterIconUrl', iconUrl);
        params.append('displayName', displayName);
        params.append('twitterId', displayId);
        let loginParam = localStorage.getItem("login_param");
        console.log(loginParam)
        params.append('kbn', loginParam);

        console.log(uid + " " + iconUrl  + " " + displayName + " " + displayId);
        axios.post(this.$root.SERVER_PATH + "login", params)
        .then((response) => {
          let resData = response.data;
          console.log(resData);
          if (resData["status"] == this.$root.API_FAILED_LOGIN) {
            //IDとPASSでログイン失敗
            // this.errorMessageData.login = "ログインに失敗しました";
            localStorage.setItem("top_param", "FAILED");
            this.$router.push("/");
            return;
          } else if (resData["status"] == this.$root.API_FAILED_NODATA) {
            //データなし（アカウント未作成）
            // this.errorMessageData.login = "アカウントが作成されていません\n新規登録を行ってください";
            localStorage.setItem("top_param", "NODATA");
            this.$router.push("/");
            return;
          }

          // if (!this.$root.checkServerResponse(response)) {
          //   return;
          // }

          localStorage.setItem("twitterId", displayId);
          localStorage.setItem("displayName", displayName);
          localStorage.setItem("top_param", "");
          this.$router.push("/");          
          // this.$root.displayName = displayName;
          // this.$root.twitterId = displayId;

          // let myDataList = resData["myQuizList"];
          // if (myDataList != null) {
          //   this.myQuizDataList = myDataList;
          //   console.log(this.myQuizDataList);
          //   this.$root.isLogin = true;
          // }
        }).catch((e) => {
          alert(e); 
        });
      }else{
        console.log('not login!!');
        this.$router.push("/");
        // this.$router.push('/signin')
      }
    })
  },
  methods : {
    initAction : function() {
      console.log(localStorage.getItem("login"));
      if (localStorage.getItem("login") != "") {
        //ログイン処理
        console.log("ログイン開始");
        localStorage.setItem("login", "");
        const provider = new TwitterAuthProvider()
        const auth = getAuth();
        setPersistence(auth, browserLocalPersistence)
        .then(() => {
          signInWithRedirect(auth, provider);
        });
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log("ooooooooooooooooooooo1");
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
      console.log("ooooooooooooooooooooo2");
    });
  },
  watch : {
  }


}
</script>

