    <p v-show="this.isDispMyData == 0" class="font_m link" v-on:click="dispMyData(1)">--- 非表示にする ---</p>
    <p v-show="this.isDispMyData == 1" class="font_m link" v-on:click="dispMyData(0)">--- 表示する ---</p>
